.loading-page {
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#253f7f), to(#9198e5));
  background-image: linear-gradient(#253f7f, #9198e5);
  z-index: 1;
}
.loading-page .loading-show {
  position: absolute;
  width: 100%;
  top: 50%;
  margin-top: -65px;
  height: 130px;
  text-align: center;
}
.loading-page .star {
  width: 120px;
  -webkit-animation-timing-function: ease-in-out;
  animation-timing-function: ease-in-out;
  -webkit-animation-name: star;
  animation-name: star;
  animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
  animation-duration: 2s;
  -webkit-animation-duration: 2s;
}
.loading-page .loading-nums {
  display: none;
  position: relative;
  top: 20px;
  margin-left: 20px;
  display: inline-block;
  width: 200px;
  height: 20px;
  vertical-align: middle;
}
.loading-page .loading-nums .star6 {
  display: inline-block;
  width: 20px;
  height: 100%;
  margin-right: 16px;
  -webkit-transform-origin: right bottom;
          transform-origin: right bottom;
  -webkit-animation: load 4s ease infinite;
          animation: load 4s ease infinite;
}
.loading-page .loading-nums .star6:last-child {
  margin-right: 0px;
}
.loading-page .loading-nums .star6:nth-child(1) {
  -webkit-animation-delay: 0.13s;
          animation-delay: 0.13s;
}
.loading-page .loading-nums .star6:nth-child(2) {
  -webkit-animation-delay: 0.26s;
          animation-delay: 0.26s;
}
.loading-page .loading-nums .star6:nth-child(3) {
  -webkit-animation-delay: 0.39s;
          animation-delay: 0.39s;
}
.loading-page .loading-nums .star6:nth-child(4) {
  -webkit-animation-delay: 0.52s;
          animation-delay: 0.52s;
}
.loading-page .loading-nums .star6:nth-child(5) {
  -webkit-animation-delay: 0.65s;
          animation-delay: 0.65s;
}
.loadingfadeOut {
  -webkit-animation-timing-function: ease-in-out;
  animation-timing-function: ease-in-out;
  -webkit-animation-name: fadeOut;
  animation-name: fadeOut;
  animation-duration: 2s;
  -webkit-animation-duration: 2s;
}
@-webkit-keyframes load {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    -webkit-transform: rotate(90deg);
  }
}
@keyframes load {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    -webkit-transform: rotate(90deg);
  }
}
@-webkit-keyframes star {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  25% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  50% {
    -webkit-transform: translateY(20px) scale(1.1, 0.9);
            transform: translateY(20px) scale(1.1, 0.9);
  }
  75% {
    ransform: translateY(10px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@keyframes star {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  25% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  50% {
    -webkit-transform: translateY(20px) scale(1.1, 0.9);
            transform: translateY(20px) scale(1.1, 0.9);
  }
  75% {
    ransform: translateY(10px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@-webkit-keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    visibility: hidden;
  }
}
@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    visibility: hidden;
  }
}
