.myintro {
  position: relative;
  height: 100%;
  width: 100%;
}
.myintro .myintro-swiper-container {
  width: 100%;
  height: 100%;
}
.myintro .mybg {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
.myintro .mytopimg {
  width: 100%;
  height: 100%;
}
.myintro .cover {
  position: absolute;
  left: 0px;
  top: 0px;
}
.myintro .into-part {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(20, 38, 70, 0.4);
  z-index: 1;
  color: #fff;
}
.myintro .into-part .myself {
  position: relative;
  text-align: center;
  top: 50%;
  margin-top: -120px;
  height: 200px;
}
.myintro .into-part .hi {
  position: absolute;
  width: 100%;
  top: 0;
  margin-bottom: 18px;
  font-size: 30px;
  font-family: 'Shadows Into Light', cursive;
}
.myintro .into-part .sayhi {
  position: absolute;
  width: 100%;
  top: 40px !important;
  margin-bottom: 18px;
  font-size: 58px;
  font-family: 'Dancing Script', cursive;
  color: #fff;
}
.myintro .into-part .job {
  position: absolute;
  width: 100%;
  top: 135px !important;
  font-size: 22px;
}
.myintro .into-part .arrowdwon {
  cursor: pointer;
  position: absolute;
  left: -36px;
  margin-left: 50%;
  bottom: 20px;
  width: 72px;
  height: 72px;
  background: url(/static/media/箭头3.5007cbba.svg) no-repeat center center;
  background-size: 100% 100%;
}
.myintro .into-part .arrowdwon.fadeIn {
  animation-delay: 4s;
  -webkit-animation-delay: 4s;
  /* Safari 和 Chrome */
  animation-timing-function: linear;
  -webkit-animation-timing-function: linear;
}
.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0.5;
  -webkit-transition: opacity 0.8s linear;
  transition: opacity 0.8s linear;
}
.fade-exit-done {
  opacity: 0;
}
