.skill {
  position: relative;
  height: 100%;
  width: 100%;
}
.skill .bg-mask {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(190, 236, 189, 0.2);
}
.skill .chart-part {
  margin: 0 auto;
  width: 700px;
  height: 400px;
}
.skill #skilldom {
  opacity: 0;
  position: relative;
  top: 50%;
  margin-top: -200px;
}
