.aboutme {
  position: relative;
  height: 100%;
  width: 100%;
}
.aboutme .butterfly {
  position: absolute;
  top: -85px !important;
  left: -74px !important;
  width: 100px;
  height: 98px;
  background: url(/static/media/butterfly.8ed3e99e.png) no-repeat center center;
  background-size: 100% 100%;
}
.aboutme .main-content {
  position: absolute;
  width: 850px;
  height: 400px;
  top: 50%;
  margin-top: -200px;
  margin-left: 50%;
  left: -400px !important;
}
.aboutme .toux {
  position: absolute;
  width: 300px;
  height: 400px;
  overflow: hidden;
  border-radius: 8px;
  box-shadow: 0 0 10px #fff;
}
.aboutme .toux img {
  position: absolute;
