  top: 0;
  left: -160px !important;
}
.aboutme .arrow {
  position: absolute;
  opacity: 0;
  width: 100px;
  top: -13px !important;
  left: 263px !important;
  -webkit-transform: rotate(2deg);
          transform: rotate(2deg);
  height: 50px;
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: 100% 100%;
}
.aboutme .me {
  position: absolute;
  opacity: 0;
