* {
  margin: 0;
  padding: 0;
}
body {
  font-family: "youyuan";
}
div {
  box-sizing: border-box;
}
a {
  text-decoration: none;
  box-sizing: border-box;
}
.clearfix:after {
  display: block;
  content: ".";
  visibility: hidden;
  height: 0;
  clear: both;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
button {
  outline: none !important;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
button:focus {
  outline: none !important;
}
ul li {
  list-style-type: none;
}
