.experience {
  padding-top: 100px;
  position: relative;
  height: 100%;
  width: 100%;
}
.experience .timepart .now {
  text-align: center;
  margin-bottom: 8px;
}
.experience .timepart .now .time {
  opacity: 0;
  font-size: 28px;
  font-family: 'Dancing Script', cursive;
  color: #ffca28;
  text-shadow: 0 0 8px #fff176;
  animation-delay: 1.6s;
  -webkit-animation-delay: 1.6s;
}
.experience .mytimeline {
  opacity: 0;
  animation-delay: 2s;
  -webkit-animation-delay: 2s;
}
.experience .timeLineTxt {
  padding: 0 20px;
  font-size: 20px;
  color: #fff;
  text-shadow: 0 0 4px #e1f5fe;
}
.experience .activetxt {
  padding: 8px 20px 0 20px;
}
.experience .ant-timeline.ant-timeline-label .ant-timeline-item-label {
  font-size: 20px;
  padding: 0 20px;
  color: #ffff8d;
  text-shadow: 0 0 4px #ffff8d;
  font-family: 'Dancing Script', cursive;
}
.experience .ant-timeline.ant-timeline-label .ant-timeline-item-label .activetime {
  padding-top: 7px;
}
.experience .ant-timeline-item {
  padding-bottom: 56px;
}
.experience .ant-timeline-item-head {
  width: 20px;
  height: 20px;
}
.experience .ant-timeline.ant-timeline-alternate .ant-timeline-item-head,
.experience .ant-timeline.ant-timeline-right .ant-timeline-item-head,
.experience .ant-timeline.ant-timeline-label .ant-timeline-item-head {
  margin-left: -8px;
}
.experience .ant-timeline-item-content {
  top: -10px;
}
.experience .ant-timeline-item-head-custom {
  background: transparent;
}
.experience .ant-timeline-item-tail {
  border-left: 2px solid #7887B0;
}
.experience .ant-timeline-item-head-blue {
  color: #45568A;
  border-color: #45568A;
}
.experience .ant-timeline.ant-timeline-pending .ant-timeline-item-last .ant-timeline-item-tail {
  border-left: 2px dotted #7887B0;
}
.experience .bottomBg {
  position: absolute;
  left: 0;
  width: 100%;
  bottom: 0;
  overflow: hidden;
  opacity: 0;
  animation-duration: 4s;
  -webkit-animation-duration: 4s;
}
.experience .bottomBg img {
  width: 100%;
}
.experience .moon {
  position: absolute;
  left: 0;
  top: 0;
  width: 268px;
  height: 200px;
  background: url(/static/media/moon.21d2b1ac.png) no-repeat center center;
  background-size: 100% 100%;
  animation-delay: 0.6s;
  -webkit-animation-delay: 0.6s;
}
.experience .moonFloat {
  -webkit-animation-timing-function: ease-in-out;
  animation-timing-function: ease-in-out;
  -webkit-animation-name: moon;
  animation-name: moon;
  animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
  animation-duration: 4s;
  -webkit-animation-duration: 4s;
}
@-webkit-keyframes moon {
  0% {
    -webkit-transform: translatex(0px);
            transform: translatex(0px);
    opacity: 1;
  }
  50% {
    -webkit-transform: translatex(-20px);
            transform: translatex(-20px);
    opacity: 0.9;
  }
  100% {
    -webkit-transform: translatex(0px);
            transform: translatex(0px);
    opacity: 1;
  }
}
@keyframes moon {
  0% {
    -webkit-transform: translatex(0px);
            transform: translatex(0px);
    opacity: 1;
  }
  50% {
    -webkit-transform: translatex(-20px);
            transform: translatex(-20px);
    opacity: 0.9;
  }
  100% {
    -webkit-transform: translatex(0px);
            transform: translatex(0px);
    opacity: 1;
  }
}
