html,
body {
  height: 100%;
}
.mycv {
  position: absolute;
  height: 100%;
  width: 100%;
  background: url(/static/media/bg.31853dad.jpg) no-repeat center center;
  background-size: cover;
}
.mycv .mycv-swiper-container {
  width: 100%;
  height: 100%;
}
@-webkit-keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
