.project {
  padding-top: 32px;
}
.project .show-project {
  position: absolute;
  width: 100%;
  margin-top: -130px;
  top: 50%;
  padding: 0 58px;
}
.project .page-tit {
  text-align: center;
}
.project .page-tit .tit {
  padding: 6px 0;
  display: inline-block;
  color: #ffca28;
  font-size: 28px;
  border-bottom: 3px solid #ffca28;
}
.project .ant-card-body {
  padding: 0;
}
.project .card-box {
  position: relative;
  width: 100%;
  height: 240px;
  box-shadow: 0 0 10px #AFB6BE;
  border-radius: 6px;
  overflow: hidden;
}
.project .card-box .mask {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.7);
  color: #fff;
  justify-content: center;
}
.project .card-box .mask .content {
  text-align: center;
  padding: 48px 18px 18px 18px;
}
.project .card-box .mask .content .tit {
  font-size: 23px;
  margin-bottom: 10px;
}
.project .card-box .img-box {
  position: relative;
  height: 100%;
  overflow: hidden;
  border-radius: 6px;
}
.project .card-box .bg1 {
  height: 100%;
  position: absolute;
  left: 50%;
  margin-left: -300px;
}
.project .card-box .bg2 {
  width: 100%;
  position: absolute;
  top: 0;
}
.project .card-box .bg3 {
  width: 100%;
  position: absolute;
  top: 0;
}
.project .card-box .bg4 {
  height: 100%;
  position: absolute;
  left: 50%;
  margin-left: -200px;
}
.project .card-box:hover .img-box {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-transition: -webkit-transform 0.5s ease-in-out;
  transition: -webkit-transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
}
.project .card-box:hover .mask {
  display: flex;
}
.project .fadeInUp {
  animation-duration: 0.7s;
  -webkit-animation-duration: 0.7s;
  /* Safari 和 Chrome */
}
.project .fadeInDown {
  animation-delay: 0.4s;
  -webkit-animation-delay: 0.4s;
  /* Safari 和 Chrome */
  animation-duration: 0.8s;
  -webkit-animation-duration: 0.8s;
  /* Safari 和 Chrome */
}
