.contact-me {
  color: #fff;
  position: relative;
  width: 100%;
  height: 100%;
}
.contact-me .leftlian {
  opacity: 0;
  position: absolute;
  width: 76%;
  height: 100%;
  left: 0;
  top: 0;
  overflow: hidden;
}
.contact-me .leftlian img {
  width: 100%;
  height: 100%;
}
.contact-me .rightlian {
  opacity: 0;
  position: absolute;
  right: 0;
  top: 0;
  width: 36%;
  height: 100%;
  overflow: hidden;
}
.contact-me .rightlian img {
  width: 100%;
  height: 100%;
}
.contact-me .moon {
  opacity: 0;
  position: absolute;
  width: 232px;
  height: 183px;
  left: 120px;
  top: 10px;
  background: url(/static/media/moon.12731c81.png) no-repeat center center;
  background-size: 100% 100%;
}
.contact-me .stars {
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  background: url(/static/media/light.8489af38.png) no-repeat center center;
  background-size: 100%;
  animation-duration: 4s;
  -webkit-animation-duration: 4s;
  animation-delay: 2s;
  -webkit-animation-delay: 2s;
}
.contact-me .moonFloat {
  -webkit-animation-timing-function: ease-in-out;
  animation-timing-function: ease-in-out;
  -webkit-animation-name: moon;
  animation-name: moon;
  animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
  animation-duration: 4s;
  -webkit-animation-duration: 4s;
  animation-delay: 1s;
  -webkit-animation-delay: 1s;
}
.contact-me .email-content {
  opacity: 0;
  position: absolute;
  width: 100%;
  text-align: center;
  height: 100px;
  top: 50%;
  margin-top: -50px;
  animation-delay: 2.5s;
  -webkit-animation-delay: 2.5s;
}
.contact-me .email-content .emailicon {
  font-size: 50px;
  animation-delay: 2.5s;
  -webkit-animation-delay: 2.5s;
  animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
  animation-duration: 2s;
  -webkit-animation-duration: 2s;
  -webkit-animation-timing-function: ease-in-out;
  animation-timing-function: ease-in-out;
}
.contact-me .email-content .email-num {
  margin-left: 20px;
  font-size: 36px;
  letter-spacing: 5px;
  font-family: 'Dancing Script', cursive;
}
@-webkit-keyframes moon {
  0% {
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
    opacity: 1;
  }
  50% {
    -webkit-transform: translate(-20px, -10px);
            transform: translate(-20px, -10px);
    opacity: 0.9;
  }
  100% {
    -webkit-transform: translate(0px);
            transform: translate(0px);
    opacity: 1;
  }
}
@keyframes moon {
  0% {
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
    opacity: 1;
  }
  50% {
    -webkit-transform: translate(-20px, -10px);
            transform: translate(-20px, -10px);
    opacity: 0.9;
  }
  100% {
    -webkit-transform: translate(0px);
            transform: translate(0px);
    opacity: 1;
  }
}
